/* width */
::-webkit-scrollbar {
    width: 10px;
}
/* Track */
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 5px grey;
            box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
    border: none;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgba(63, 81, 181, .4);
    border-radius: 10px;
    max-height: 20px;
}

